<template>
  <div class="tabbar">
    <van-tabbar v-model="active" @click="handleChange" fixed router >
      <van-tabbar-item
        v-for="(item, index) in tabData"
        :icon="item.icon"
        :to="item.path"
        tabbar-item-icon-size:50px
        :key="index">
        <span :class="defaultActive === index ? active:''">{{item.title}}</span>
        <template slot="icon" slot-scope="props">
          <img :src="props.active ? item.active : item.normal">
        </template>
      </van-tabbar-item>
    </van-tabbar>
  </div>
</template>
 
<script>
export default {
  name: 'TabBar',
  props: {
    defaultActive: {
      type: Number,
      default: 0
    },
    tabData: {
      type: Array,
      default: () => []
    }
  },
  data() {
    return {
      active: this.defaultActive
    }
  },
  methods: {
    handleChange(val) {
      this.$emit('change', val);
    }
  }
}
</script>
 
<style scoped lang='less'>
.van-tabbar{
  width: 100%;
  height: 10%;
  .van-tabbar-item{
    font-size: 18px;
    font-weight: bolder;
  }
  

}

.van-tabbar-item__icon img{
  width: 12vw;
  height: 12vw;
}
.van-tabbar-item--active {
  color: #0E5CD8;
}

</style>